import PrivilegeEnum from "../enums/PrivilegeEnum";
import productDetailsView from "../views/productDetailsView";
import getComments from "../services/jsonPlaceHolderAPI";
import createRemoveButton from "../components/removeButton";
export default function ProductDetailsViewLogic(store, mainContent) {
    var productID = window.location.href.split("?")[1].split("=");
    var product = store.getProductById(productID[1]);
    //load the view
    mainContent.innerHTML = productDetailsView(product[0]);
    //get the comments
    var productCommentsContainer = document.getElementById("product-comments-container");
    getComments(productCommentsContainer);
    //add events to buy buttons
    var buyButtons = document.getElementsByName("buy-button");
    buyButtons.forEach(function (buyButton) {
        buyButton.addEventListener("click", function () {
            store.cart.add(product[0]);
            var productQuantity = document.getElementById("cart-quantity");
            productQuantity.innerHTML = " &nbsp " + store.cart.showAll().length;
        });
        if (store.user.privilege == PrivilegeEnum.admin) {
            buyButton.after(createRemoveButton());
        }
    });
    var infoContainer = document.getElementById("info-container");
    var paragraph = document.getElementById("info-container-paragraph");
    var infoContainerButton = document.getElementById("info-container-button");
    var domElements = document.querySelectorAll("header, main, footer");
    var displayInfoContainer = function (text) {
        domElements.forEach(function (element) {
            element.style.opacity = "0.4";
            element.style.pointerEvents = "none";
        });
        infoContainer.classList.add("display-info-container");
        paragraph.innerText = text;
    };
    if (infoContainerButton) {
        infoContainerButton.addEventListener("click", function () {
            domElements.forEach(function (element) {
                element.setAttribute("style", "");
            });
            infoContainer.classList.remove("display-info-container");
        });
    }
    var removeButtons = document.getElementsByName("remove-button");
    if (removeButtons != null) {
        removeButtons.forEach(function (button) {
            button.addEventListener("click", function () {
                store.removeProduct(productID[1]);
                displayInfoContainer("El producto ha sido eliminado correctamente.");
            });
        });
    }
}
