import Product from "../entities/Product";
import ProductTypeEnum from "../enums/ProductTipeEnum";
import newProductView from "../views/newProductView";
export default function NewProductViewLogic(store, mainContent) {
    mainContent.innerHTML = newProductView();
    var typeSelector = document.getElementById("type-selector");
    for (var item in Object.values(ProductTypeEnum)) {
        var newOption = document.createElement("option");
        newOption.value = Object.keys(ProductTypeEnum)[item];
        newOption.innerHTML = Object.values(ProductTypeEnum)[item];
        typeSelector.appendChild(newOption);
    }
    var newProductForm = document.getElementById("new-product-form");
    var productName = newProductForm["product-name"];
    var productType = newProductForm["product-type"];
    var productPrice = newProductForm["product-price"];
    var productDescrition = newProductForm["product-description"];
    var productImage = newProductForm["product-image"];
    var productThumbnail = newProductForm["product-thumbnail"];
    var productStock = newProductForm["product-stock"];
    var productDiscount = newProductForm["product-discount"];
    var newProductSubmitButton = document.getElementById("new-product-submit-button");
    var infoContainer = document.getElementById("info-container");
    var paragraph = document.getElementById("info-container-paragraph");
    var domElements = document.querySelectorAll("header, main, footer");
    var displayInfoContainer = function (text) {
        domElements.forEach(function (element) {
            element.style.opacity = "0.4";
            element.style.pointerEvents = "none";
        });
        infoContainer.classList.add("display-info-container");
        paragraph.innerText = text;
    };
    newProductSubmitButton.addEventListener("click", function (event) {
        event.preventDefault();
        try {
            var newProduct = new Product();
            newProduct.id = window.crypto
                .getRandomValues(new Uint32Array(1))[0]
                .toString();
            newProduct.name = productName.value;
            newProduct.type = productType.value;
            newProduct.price = parseFloat(productPrice.value);
            newProduct.description = productDescrition.value;
            newProduct.image = productImage.value;
            newProduct.thumbnail = productThumbnail.value;
            newProduct.stock = parseInt(productStock.value);
            newProduct.discount = parseFloat(productDiscount.value);
            store.newProduct(newProduct);
            displayInfoContainer("El producto ha sido creado correctamente.");
        }
        catch (error) {
            displayInfoContainer("" + error);
        }
    });
    var infoContainerButton = document.getElementById("info-container-button");
    if (infoContainerButton) {
        infoContainerButton.addEventListener("click", function () {
            domElements.forEach(function (element) {
                element.setAttribute("style", "");
            });
            infoContainer.classList.remove("display-info-container");
        });
    }
}
